.parent {
    display: flex;
    flex-direction: column;
}

.table {
    margin: 24px;
    border: 1px solid lightgrey;
}

.child-grey {
    padding-top: 4px;
    padding-bottom: 4px;
    display: flex;
    flex-direction: row;
    background-color: lightgrey;
}

.child-white {
    padding-top: 4px;
    padding-bottom: 4px;
    display: flex;
    flex-direction: row;
    background-color: white;
}

.child-title {
    flex: 1;
}

.child-content {
    flex: 2;
}

.phone-parent {
    margin: 24px;
    display: flex;
    flex-direction: row;
    align-items: center;
}